.render {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}
.render-box {
  padding-top: 20px;
  padding-bottom: 20px;
  display: flex;
  flex-direction: column;
  // @media screen and (min-width: 320px) {
  //   padding-bottom: 20px;
  // }

  @media screen and (min-width: 768px) {
    padding-top: 60px;
    padding-bottom: 25px;
    flex-direction: row;
    flex-wrap: wrap;
    margin-top: -30px;
    margin-left: -30px;
  }
}

.card__image {
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
  object-fit: cover;
  border-radius: 5px;
}
.film-card {
  // outline: 2px solid rgb(0, 153, 255);

  &__img-zaglushka {
    height: 398px;
    border-radius: 5px;
    background-color: rgb(111, 175, 172);
  }

  &__discribe-container {
    padding-top: 10px;
  }

  &__info {
    display: flex;
  }

  &__film-name {
    text-transform: uppercase;
  }

  &__film-name,
  &__film-ganre {
    font-weight: 500;
    font-size: 12px;
    line-height: 16px;
    color: $primary-text-color;
  }

  &__film-caption {
    display: flex;
    align-items: center;
    justify-content: start;
    font-size: 12px;
    line-height: 1.33;
    color: #ff6b08;
    padding: 1px 0;
  }

  &__film-items {
    display: flex;
    justify-content: start;
  }

  &__film-item {
    margin-right: 5px;
  }

  &__film-date {
    width: 33px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: unset;
  }

  &__film-date:before {
    content: '';
    display: inline-block;
    width: 1px;
    height: 11px;
    margin-right: 5px;
    background-color: #ff6b08;
  }

  &__film-item:not(:last-child) {
    margin-right: 3px;
  }

  &__film-ganre {
    display: flex;
    color: $accent-modal-color;
    width: 33px;
    overflow: hidden;
  }

  &__film-rating {
    width: 36px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 5px;
    margin-left: 10px;
    font-weight: 500;
    font-size: 12px;
    line-height: 14px;
    background-color: $accent-modal-color;
    color: $header-text-color;
  }

  @media screen and (min-width: 320px) {
    width: 280px;
    margin-bottom: 20px;
  }

  @media screen and (min-width: 768px) {
    width: 294px;
    flex-basis: calc(100% / 2 - 30px);
    margin-top: 30px;
    margin-left: 30px;
    margin-bottom: 0;
  }

  @media screen and (min-width: 1024px) {
    width: 274px;
    flex-basis: calc(100% / 3 - 30px);
  }
}
